body {
	font-family: "Helvetica Neue", Helvetica, sans-serif;
	font-size: 20px;
  margin: 0;
  padding: 0;
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #fdfdfd;
  }
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #fdfdfd;
  }
}



